import React, { useEffect, useState } from "react"
import { Row, Col, Form } from "antd"
import { withRouter, Link } from "react-router-dom"
import { Formik } from "formik"
import styled from "styled-components"
import { useMutation } from "@apollo/react-hooks"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import * as yup from "yup"

import { Section, Heading, Button, Alert, Logo } from "components"
import TextInput from "components/forms/TextInput"
import { mutateLogin } from "queries/auth"
import { authUserOptions } from "store/actions/authActions"
import { mobile } from "helpers"
import { country } from "App"
import loginImage from "assets/images/for-login.svg"

const BackgroundImage = styled.div`
	background-image: ${`url(${loginImage})`};
	background-size: 25em;
	background-repeat: no-repeat;
	background-position: center;
	position: relative;
	height: 100vh;
	img {
		position: absolute;
		left: 30px;
		top: 20px;
		cursor: pointer;
	}
`

const StyledRow = styled(Row)`
	background-color: #fff;
`

function Signin({ history, location, i18n }) {
	const params = new URLSearchParams(location.search)
	const previousRoute = (location.state || {}).previousRoute || ""

	const [theError, setError] = useState(null)
	const [setLogin, { loading }] = useMutation(mutateLogin, authUserOptions(setError, params.get("from"), previousRoute))

	const notLoggedInText = (location.state || {}).notLoggedIn

	const handleLogin = values => {
		setLogin({ variables: { ...values } })
	}

	useEffect(() => {
		if (localStorage.getItem("token")) {
			history.push(`/${country}`)
		}

		// if ((location.state || {}).loggedout) {
		// 	message.error("Your session is expired. Please re-login to continue")
		// }
	}, [history])

	return (
		<StyledRow>
			{!mobile && (
				<Col lg={12}>
					<BackgroundImage>
						<Logo onClick={() => history.push(`/${country}`)} />
					</BackgroundImage>
				</Col>
			)}
			<Col lg={12}>
				{mobile && (
					<Row type="flex" justify="center" style={{ paddingTop: "2em" }}>
						<Col lg={12}>
							<Logo onClick={() => history.push(`/${country}`)} />
						</Col>
					</Row>
				)}
				<Section style={{ marginTop: "3em", marginBottom: 0 }}>
					<Heading
						content={<Trans>Welcome back!</Trans>}
						subheader={<Trans>Please login into your account to continue using Cudy</Trans>}
					/>
				</Section>

				{notLoggedInText && (
					<Section>
						<Alert type="warning" showIcon message="Login first" description={notLoggedInText} className="mb0" />
					</Section>
				)}

				{/* <Section>
                    <Row gutter={16}>
                        <Col lg={12}>
                            <Button block social="facebook" icon="facebook">
                                Sign in with Facebook
                            </Button>
                        </Col>
                        <Col lg={12}>
                            <Button block social="google" icon="google">
                                Sign in with Google
                            </Button>
                        </Col>
                    </Row>
                </Section>
                <Section centered>OR</Section> */}

				<Section marginBottom={0}>
					<Formik onSubmit={handleLogin} validationSchema={validationSchema(i18n)} initialValues={{ email: "", password: "" }}>
						{({ handleSubmit }) => (
							<Form onSubmit={handleSubmit} layout="vertical">
								<TextInput
									type="email"
									label={<Trans>Your email</Trans>}
									name="email"
									placeholder={i18n._(t`Enter your email`)}
								/>
								<TextInput
									password
									type="password"
									label={<Trans>Your password</Trans>}
									name="password"
									placeholder={i18n._(t`Enter your password`)}
								/>
								{theError && theError.code === 10010 && (
									<Section paddingHorizontal={0}>
										<Alert
											message="Not verified"
											description={
												<Trans>
													<span>
														It looks like your account is not verified yet.{" "}
														<Link to={`/${country}/account/resend_email`}>
															Send me another email verification
														</Link>
													</span>
												</Trans>
											}
											type="error"
											showIcon
										/>
									</Section>
								)}
								<Section paddingHorizontal={0} textAlign="right" style={{ paddingTop: 0 }} marginBottom="very">
									<Link to={`/${country}/forgot`}>
										<Trans>Forgot you password?</Trans>
									</Link>
								</Section>
								<Row gutter={24}>
									<Col lg={12} style={{ marginBottom: mobile && "1em" }}>
										<Button type="primary" htmlType="submit" loading={loading} block>
											<Trans>Login now</Trans>
										</Button>
									</Col>{" "}
									<Col lg={12}>
										<Button block type="ghost" htmlType="button" onClick={() => history.push(`/${country}/register`)}>
											<Trans>Sign up instead</Trans>
										</Button>
									</Col>
								</Row>
							</Form>
						)}
					</Formik>
				</Section>
			</Col>
		</StyledRow>
	)
}

const validationSchema = i18n =>
	yup.object().shape({
		email: yup
			.string()
			.required(i18n._(t`This is required`))
			.email(i18n._(t`Please enter a valid email`)),
		password: yup.string().required(i18n._(t`This is required`))
	})

export default withI18n()(withRouter(Signin))
